import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

let computeRange = (row, state) => {
  return row[5] >= state.years[0] && row[6] <= state.years[1];
}

let computeDiff = (row, state) => {
  return state.differents ? row[7] == state.differents : true;
}

let computeUniverFrom = (row, state) => {
  return state.from != null ? row[3] == state.from : true;
}

let computeUniverTo = (row, state) => {
  return state.to != null ? row[4] == state.to : true;
}


export const store = new Vuex.Store({
  state: {
    level: '/Pregrado.json',
    differents: true,
    from: null,
    to: null,
    years: [2012, 2018],
    data : [],
    available: false
  },
  mutations: {
    updateLevel(state, level) {
      state.level = level
    },
    updateDiff(state, differents) {
      state.differents = differents
    },
    updateFrom(state, from) {
      state.from = from
    },
    updateTo(state, to) {
      state.to = to
    },
    updateYears(state, years) {
      state.years = years
    },
    updateData(state, data) {
      state.data = data
    },
    updateStatus(state, available) {
      state.available = available
    }
  },
  actions:{
    async loadData({commit, state}){
      const response = await axios.get( state.level);

      let data = [['From', 'To', 'Alumnos']];
      for(let row of response.data.data){
          if(computeRange(row, state) &&
             computeDiff(row, state) &&
             computeUniverFrom(row, state) &&
             computeUniverTo(row, state)){
             data.push([row[0], row[1], row[2]])
          }
      }
      commit('updateData', data);
    }
  }
  ,
  getters: {
    level: state => state.level,
    differents: state => state.differents,
    from: state => state.from,
    to: state => state.to,
    years: state => state.years,
    data: state => state.data,
    available: state => state.available,
  }
})
