<template>
  <v-container fluid grid-list-xl>
      <v-layout wrap align-center>
        <v-flex xs12 sm1 d-flex>
          <v-radio-group v-model="level" v-on:click="updateLevel">
            <v-radio
              v-for="n in ['Pregrado', 'Postgrado']"
              :key="n"
              :label="`${n}`"
              :value="`/dataviz/${n}.json`"
            ></v-radio>
          </v-radio-group>
        </v-flex>
        <v-flex xs12 sm3 d-flex>
          <v-select v-model="from" v-on:change="updateFrom"
            :items="universities"
            label="Universidad Origen"
            clearable
            outline
          ></v-select>
        </v-flex>
        <v-flex xs12 sm3 d-flex>
          <v-select v-model="to" v-on:change="updateTo"
            :items="universities"
            label="Universidad Destino"
            clearable
            outline
          ></v-select>
        </v-flex>
        <v-flex xs12 sm3 d-flex>
              <v-flex shrink style="width: 100px">
                <v-text-field
                  v-model="years[0]"
                  class="mt-0"
                  hide-details
                  single-line
                  type="number"
                ></v-text-field>
              </v-flex>

              <v-flex>
                <v-range-slider  v-on:change="updateYears"
                  v-model="years"
                  :max="2018"
                  :min="2007"
                  :step="1"
                ></v-range-slider>
              </v-flex>

              <v-flex shrink style="width: 100px">
                <v-text-field
                  v-model="years[1]"
                  class="mt-0"
                  hide-details
                  single-line
                  type="number"
                ></v-text-field>
              </v-flex>
        </v-flex>
        <v-flex xs12 sm2 d-flex>
          <v-checkbox v-model="differents"  label="Excluir Similares" v-on:change="updateDiff"></v-checkbox>
        </v-flex>
      </v-layout>

    </v-container>
</template>

<script>
export default {
   name: 'Controls',
   data: function () {
    return {
     universities: [],
     years: [2012, 2018],
     from: [2012, 2018],
     to: [2012, 2018],
     differents:true,
     level:'/Pregrado.json'
    }
   },
   methods: {
    updateLevel: function() {
      this.$store.commit('updateLevel', this.level);
      this.$store.dispatch('loadData');
      this.$store.commit('updateStatus', false);
    },
    updateDiff: function() {
      this.$store.commit('updateDiff', this.differents);
      this.$store.dispatch('loadData');
      this.$store.commit('updateStatus', false);
    },
    updateFrom: function() {
      this.$store.commit('updateFrom', this.from);
      this.$store.dispatch('loadData');
      this.$store.commit('updateStatus', false);
    },
    updateTo: function() {
      this.$store.commit('updateTo', this.to);
      this.$store.dispatch('loadData');
      this.$store.commit('updateStatus', false);
    },
    updateYears: function() {
      this.$store.commit('updateYears', this.years);
      this.$store.dispatch('loadData');
      this.$store.commit('updateStatus', false);
    }
  },
   created() {
       fetch('/universities.json')
       .then(r => r.json())
       .then(json => { this.universities = json;})
   },
}
</script>
